//SEPERARTÖR -----------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
.footer {
    width: 100%;
    height: 50px;
    padding: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-top: 2px solid black;

    & .instagram-icon {
        width: 25px;
        height: 25px;
        fill: rgba($color: #000000, $alpha: .6);
        margin-right: 20px;
    }

    & .github-icon {
        width: 25px;
        height: 25px;
        fill: rgba($color: #000000, $alpha: .6);
        margin-right: 20px;
    }

    & .linkedin-icon {
        width: 25px;
        height: 25px;
        fill: rgba($color: #000000, $alpha: .6);
    }
}

.footerDark {
    width: 100%;
    height: 50px;
    padding: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $DarkThemeBackgraundColor;
    border-top: 2px solid white;

    & .instagram-icon {
        width: 25px;
        height: 25px;
        fill: white;
        margin-right: 20px;
    }

    & .github-icon {
        width: 25px;
        height: 25px;
        fill: white;
        margin-right: 20px;
    }

    & .linkedin-icon {
        width: 25px;
        height: 25px;
        fill: white;
    }
}


